import React, { Component } from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';
import styled from 'styled-components';
import Form from './styles/Form';
import Btn from './styles/Btn';

const CreateUserStyles = styled.div`
  h4 {
    color: ${props => props.theme.red};
  }

  h5 {
    margin-bottom: 1rem;
  }

  .form-row.permissions .row-full {
    @media screen and (min-width: 768px) {
      display: flex;
      flex-direction: row;

      label p {
        margin-right: 1rem;
        margin-left: 0;
      }
    }
  }
`;

const CREATE_USER_MUTATION = gql`
  mutation CREATE_USER_MUTATION(
    $firstName: String!
    $lastName: String
    $company: String
    $email: String
    $address: String
    $phone: String
    $username: String!
    $password: String!
    $permissions: String!
    $canOrder: Boolean!
  ) {
    createUser(
      data: {
        firstName: $firstName
        lastName: $lastName
        company: $company
        email: $email
        address: $address
        phone: $phone
        username: $username
        password: $password
        permissions: $permissions
        canOrder: $canOrder
        active: true
      }
    ) {
      id
    }
  }
`;

class CreateUser extends Component {
  state = {
    firstName: '', // required
    lastName: '',
    username: '', // required
    company: '',
    email: '',
    permissions: '', // required
    canOrder: false, // required - needs to be false by default
    phone: '',
    address: '',
    password: '', // required
    confirmPassword: '', // required
    error: {},
  };

  handleChange = e => {
    const { name, type, value } = e.target;
    const val = type === 'number' ? parseFloat(value) : value;
    this.setState({ [name]: val });
  };

  handleCheckbox = e => {
    const { name } = e.target;
    this.setState({ [name]: !this.state.canOrder });
  };

  resetState = e => {
    this.setState({
      firstName: '', // required
      lastName: '',
      username: '', // required
      company: '',
      email: '',
      permissions: '', // required
      canOrder: false, // required - needs to be false by default
      phone: '',
      address: '',
      password: '', // required
      confirmPassword: '', // required
      error: {},
    });

    window.scrollTo(0, 0);
  };

  validateForm = () => {
    let isValid;
    const error = {};

    if (!this.state.firstName.length > 0) {
      error.firstName = 'First Name is required.';
    } else if (this.state.firstName.length < 3) {
      error.firstName =
        'First Name is too short. Must be at least two characters long';
    }

    if (!this.state.username.length > 0) {
      error.username = 'Username is required.';
    } else if (!this.state.username.length < 2) {
      error.username =
        'Username is too short. Must be at least two characters long';
    }

    if (!this.state.permissions) {
      error.permissions = 'Must set permissions for this user.';
    }

    if (!this.state.password > 0) {
      error.password = 'Password is required.';
    }

    if (!this.state.confirmPassword > 0) {
      error.confirmPassword = 'Confirmation password is required.';
    }

    if (this.state.password.length > 0 && this.state.password.length < 6) {
      error.password = 'Password must be atleast 6 characters long.';
    } else if (this.state.password !== this.state.confirmPassword) {
      error.confirmPassword = "Passwords don't match";
    }

    this.setState({ error });
  };

  render() {
    const errorMessage = this.state.error;
    const disabledBtn =
      Object.entries(errorMessage).length === 0 &&
      errorMessage.constructor === Object;

    return (
      <Mutation mutation={CREATE_USER_MUTATION} variables={this.state}>
        {(createUser, { loading, error }) => (
          <CreateUserStyles>
            <Form
              id="new_user_info"
              method="post"
              onSubmit={async e => {
                e.preventDefault();
                this.validateForm();

                if (!errorMessage) {
                  const res = await createUser();
                  this.resetState(e);
                }
              }}
            >
              <fieldset disabled={loading} aria-busy={loading}>
                <h4>Add New User</h4>
                <div className="form-body">
                  <div className="form-section">
                    <h5>User Information</h5>
                    <div className="form-row info">
                      <div className="row-half">
                        <label htmlFor="firstName">
                          <p>First Name</p>
                        </label>
                        <input
                          type="text"
                          id="firstName"
                          name="firstName"
                          value={this.state.firstName}
                          onChange={this.handleChange}
                          onBlur={this.validateForm}
                          className={errorMessage.firstName ? 'error' : ''}
                        />
                        <p className="error">
                          {errorMessage.firstName && errorMessage.firstName}
                        </p>
                      </div>
                      <div className="row-half">
                        <label htmlFor="lastName">
                          <p>
                            Last Name <strong>&nbsp;(Optional)</strong>
                          </p>
                        </label>
                        <input
                          type="text"
                          id="lastName"
                          name="lastName"
                          value={this.state.lastName}
                          onChange={this.handleChange}
                        />
                      </div>
                    </div>

                    <div className="form-row info">
                      <div className="row-half">
                        <label htmlFor="username">
                          <p>Username</p>
                        </label>
                        <input
                          type="text"
                          id="username"
                          name="username"
                          value={this.state.username}
                          onChange={this.handleChange}
                        />
                      </div>

                      <div className="row-half">
                        <label htmlFor="company">
                          <p>
                            Company <strong>&nbsp;(Optional)</strong>
                          </p>
                        </label>
                        <input
                          type="text"
                          id="company"
                          name="company"
                          value={this.state.company}
                          onChange={this.handleChange}
                        />
                      </div>
                    </div>

                    <div className="form-row info">
                      <div className="row-full">
                        <label htmlFor="email">
                          <p>
                            Email Address <strong>&nbsp;(Optional)</strong>
                          </p>
                        </label>
                        <input
                          type="email"
                          id="email"
                          name="email"
                          value={this.state.email}
                          onChange={this.handleChange}
                        />
                      </div>
                    </div>
                  </div>

                  <div className="form-section">
                    <h5>Permissions</h5>
                    <div className="form-row info permissions">
                      <div className="row-full">
                        <label htmlFor="customerPermissions">
                          Customer
                          <input
                            type="radio"
                            name="permissions"
                            id="customerPermissions"
                            value="CUSTOMER"
                            checked={
                              this.state.permissions === 'CUSTOMER' && 'checked'
                            }
                            onChange={this.handleChange}
                          />
                        </label>
                        <label htmlFor="employeePermissions">
                          Employee
                          <input
                            type="radio"
                            name="permissions"
                            id="employeePermissions"
                            checked={
                              this.state.permissions === 'EMPLOYEE' && 'checked'
                            }
                            value="EMPLOYEE"
                            onChange={this.handleChange}
                          />
                        </label>
                        <label htmlFor="adminPermissions">
                          Administrator
                          <input
                            type="radio"
                            name="permissions"
                            id="adminPermissions"
                            checked={
                              this.state.permissions === 'ADMIN' && 'checked'
                            }
                            value="ADMIN"
                            onChange={this.handleChange}
                          />
                        </label>
                      </div>
                    </div>

                    <div className="form-row info">
                      <div className="row-full can-order">
                        <label htmlFor="canOrder">
                          <p>Is this user allowed to create their own order?</p>
                        </label>
                        <input
                          id="canOrder"
                          name="canOrder"
                          type="checkbox"
                          checked={this.state.canOrder === true && 'checked'}
                          onChange={this.handleCheckbox}
                        />
                      </div>
                    </div>
                  </div>

                  {/* TODO: check privileges ?? */}

                  <div className="form-section">
                    <h5>Contact Information</h5>
                    <div className="form-row info">
                      <div className="row-full">
                        <label htmlFor="phone">
                          <p>
                            Phone Number <strong>&nbsp;(Optional)</strong>
                          </p>
                        </label>
                        <input
                          type="text"
                          id="phone"
                          name="phone"
                          value={this.state.phone}
                          onChange={this.handleChange}
                        />
                      </div>
                    </div>

                    <div className="form-row info">
                      <div className="row-full">
                        <label htmlFor="address">
                          <p>
                            Street Address <strong>&nbsp;(Optional)</strong>
                          </p>
                        </label>
                        <input
                          type="text"
                          id="address"
                          name="address"
                          value={this.state.address}
                          onChange={this.handleChange}
                        />
                      </div>
                    </div>
                  </div>

                  <div className="form-section">
                    <h5>Password</h5>
                    <div className="form-row info">
                      <div className="row-half">
                        <label htmlFor="password">
                          <p>
                            {this.props.action === 'update' ? 'New ' : ''}
                            Password
                          </p>
                        </label>
                        <input
                          type="password"
                          id="password"
                          name="password"
                          value={this.state.password}
                          onChange={this.handleChange}
                        />
                      </div>
                      <div className="row-half">
                        <label htmlFor="confirmPassword">
                          <p>Confirm Password</p>
                        </label>
                        <input
                          type="password"
                          id="confirmPassword"
                          name="confirmPassword"
                          value={this.state.confirmPassword}
                          onChange={this.handleChange}
                        />
                      </div>
                    </div>
                  </div>
                </div>

                <div className="form-footer">
                  <Btn
                    type="submit"
                    id="createUser"
                    name="createUser"
                    disabled={!disabledBtn}
                  >
                    Submit
                  </Btn>
                  <Btn
                    type="cancel"
                    id="cancelUser"
                    name="cancelUser"
                    onClick={this.handleCancel}
                  >
                    Cancel
                  </Btn>
                </div>
              </fieldset>
            </Form>
          </CreateUserStyles>
        )}
      </Mutation>
    );
  }
}

export default CreateUser;
